html,
body {
  background-color: transparent;
  background-color: transparent;
}

body {
  -webkit-font-smoothing: antialiased;
  font-family: "Oswald", sans-serif;
  font-size: 10px;
  color: #333333;
}

@media (min-width: 640px) {
  body {
    font-size: 14px;
  }
}

ol,
ul,
li,
p,
a,
span {
  font-size: 10px;
}

@media (min-width: 640px) {
  ol,
  ul,
  li,
  p,
  a,
  span {
    font-size: 14px;
  }
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", sans-serif;
}

a {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  color: #333333;
  cursor: pointer;
  outline: none;
  text-decoration: none;
}

a:active, a:hover {
  color: #4d4d4d;
}

img {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.clearfix:before, .comments-area .comment-respond .respond-inputs:before, .container-single-post-nav .nav-links:before,
.clearfix:after,
.comments-area .comment-respond .respond-inputs:after,
.container-single-post-nav .nav-links:after {
  content: " ";
  display: table;
}

.clearfix:after, .comments-area .comment-respond .respond-inputs:after, .container-single-post-nav .nav-links:after {
  clear: both;
}

header {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #e3daec;
}

header .header-top form {
  float: right;
  position: relative;
  display: inline-block;
  width: 81%;
  height: 22px;
}

header .header-top form.active input {
  width: 100%;
  padding: 1px 25px 1px 5px;
}

header .header-top input {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  width: 0;
  height: 22px;
  padding: 0;
  transition: width 0.4s ease-in-out, padding 0.4s ease-in-out;
}

header .header-top input:focus {
  width: 100%;
  padding-right: 50px;
}

header .header-top span {
  position: absolute;
  top: 0;
  right: 0;
  width: 22px;
  height: 22px;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #bba3d0;
  cursor: pointer;
}

header .header-top span.active, header .header-top span:hover {
  background-color: green;
}

header .header-top a:not([href="/"]) {
  float: right;
  margin-left: 2%;
  text-transform: uppercase;
}

header .header-bottom {
  padding-top: 30px;
}

header .header-bottom ul.header-continents {
  list-style-type: none;
  margin: 0;
}

header .header-bottom li {
  position: relative;
  float: left;
  width: 14.28%;
  text-align: center;
}

header .header-bottom li:last-child {
  border-right: none;
}

header .header-bottom li:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 2px solid #bba3d0;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}

header .header-bottom li:hover:before {
  opacity: 1;
}

header .header-bottom a {
  display: block;
  text-transform: uppercase;
  padding: 5px 0 10px;
}

.comments-area .container-comment-area {
  margin-top: 60px;
  margin-bottom: 60px;
  padding: 0 60px 30px;
  border: 5px solid #000000;
}

.comments-area .container-comments-title {
  margin: -24px 0 0;
  text-align: center;
}

.comments-area h2.comments-title {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  padding: 0 30px;
  text-transform: uppercase;
  background-color: #ffffff;
}

.comments-area .container-comment-list {
  list-style-type: none;
  margin: 0;
}

.comments-area li.comment {
  margin-top: 40px;
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid #f4f4f4;
}

.comments-area li.comment .vcard {
  margin: 0;
  padding: 0;
  border: none;
}

.comments-area li.comment .vcard img {
  border-radius: 50%;
}

.comments-area li.comment .author-name,
.comments-area li.comment .date,
.comments-area li.comment .time {
  margin-top: 5px;
  margin-bottom: 0;
}

.comments-area li.comment .author-name {
  text-transform: uppercase;
}

.comments-area .comment-respond {
  position: relative;
  padding: 50px 23% 30px;
}

.comments-area .comment-respond:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  margin-left: -0.9375rem;
  margin-right: -0.9375rem;
  background-color: #f4f4f4;
}

.comments-area .comment-respond p {
  margin-bottom: 20px;
}

.comments-area .comment-respond .comment-reply-title {
  text-align: center;
  text-transform: uppercase;
}

.comments-area .comment-respond textarea[name="comment"],
.comments-area .comment-respond input[name="author"],
.comments-area .comment-respond input[name="email"] {
  background: #ffffff;
  border: 1px solid #e4e4e4;
}

.comments-area .comment-respond textarea[name="comment"] {
  height: 150px;
  margin-bottom: 10px;
  padding: 10px 13px;
  resize: none;
}

.comments-area .comment-respond input[type="submit"] {
  margin: 0;
  padding: 0 26px;
  height: 50px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 50px;
  font-size: 15px;
  border: 0;
}

.comments-area .comment-respond .respond-inputs {
  margin: 0 -5px;
}

.comments-area .comment-respond .comment-form-author,
.comments-area .comment-respond .comment-form-email {
  float: left;
  width: 50%;
  padding: 0 5px;
}

.comments-area .comment-respond .comment-form-author input,
.comments-area .comment-respond .comment-form-email input {
  margin: 0;
}

.comments-area .comment-respond .form-submit {
  text-align: center;
}

.container-single-post-nav .navigation.post-navigation h2 {
  display: none !important;
}

.container-single-post-nav .nav-links {
  margin-top: 30px;
}

.container-single-post-nav .nav-previous {
  float: left;
}

.container-single-post-nav .nav-next {
  float: right;
}

.gallery-overview .bottom-margin {
  margin-bottom: 30px;
}

.gallery-images {
  padding-top: 20px;
  text-align: center;
}

.gallery-images img {
  margin: 0.5%;
}

.gallery-images .gallery-pagination-top,
.gallery-images .gallery-pagination-bottom {
  padding-top: 20px;
  padding-bottom: 20px;
}

.gallery-images .gallery-pagination-top span, .gallery-images .gallery-pagination-top a,
.gallery-images .gallery-pagination-bottom span,
.gallery-images .gallery-pagination-bottom a {
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
}

.gallery-images .gallery-pagination-top span,
.gallery-images .gallery-pagination-bottom span {
  border-bottom: 1px solid #cfbede;
}

.gallery-images .gallery-pagination-top a:hover,
.gallery-images .gallery-pagination-bottom a:hover {
  color: #e3daec;
}

.archive .insights .post-insight,
.archive .insights .post-eating-ethnic,
.archive .eating-ethnic .post-insight,
.archive .eating-ethnic .post-eating-ethnic {
  padding-top: 60px;
  text-align: center;
}

.archive .insights .post-insight .post-meta,
.archive .insights .post-eating-ethnic .post-meta,
.archive .eating-ethnic .post-insight .post-meta,
.archive .eating-ethnic .post-eating-ethnic .post-meta {
  padding: 20px 0;
}

.archive .insights .post-insight .post-meta span,
.archive .insights .post-eating-ethnic .post-meta span,
.archive .eating-ethnic .post-insight .post-meta span,
.archive .eating-ethnic .post-eating-ethnic .post-meta span {
  margin-right: 20px;
}

.archive .insights .post-insight .post-meta-category,
.archive .insights .post-eating-ethnic .post-meta-category,
.archive .eating-ethnic .post-insight .post-meta-category,
.archive .eating-ethnic .post-eating-ethnic .post-meta-category {
  font-family: "Pacifico", cursive;
  text-transform: capitalize;
}

.archive .insights .post-insight .post-meta-category a,
.archive .insights .post-eating-ethnic .post-meta-category a,
.archive .eating-ethnic .post-insight .post-meta-category a,
.archive .eating-ethnic .post-eating-ethnic .post-meta-category a {
  font-size: 24px;
}

.archive .insights .post-insight a.post-title,
.archive .insights .post-eating-ethnic a.post-title,
.archive .eating-ethnic .post-insight a.post-title,
.archive .eating-ethnic .post-eating-ethnic a.post-title {
  display: block;
}

.archive .insights .post-insight .post-excerpt,
.archive .insights .post-eating-ethnic .post-excerpt,
.archive .eating-ethnic .post-insight .post-excerpt,
.archive .eating-ethnic .post-eating-ethnic .post-excerpt {
  padding: 10px 0;
}

.archive .insights .post-insight .post-meta-date,
.archive .insights .post-eating-ethnic .post-meta-date,
.archive .eating-ethnic .post-insight .post-meta-date,
.archive .eating-ethnic .post-eating-ethnic .post-meta-date {
  text-transform: uppercase;
}

.archive .insights .post-insight .read-more,
.archive .insights .post-eating-ethnic .read-more,
.archive .eating-ethnic .post-insight .read-more,
.archive .eating-ethnic .post-eating-ethnic .read-more {
  position: relative;
  text-transform: uppercase;
}

.archive .insights .post-insight .read-more:after,
.archive .insights .post-eating-ethnic .read-more:after,
.archive .eating-ethnic .post-insight .read-more:after,
.archive .eating-ethnic .post-eating-ethnic .read-more:after {
  content: "";
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  margin-left: 5px;
  border-style: solid;
  border-width: 8px 0 8px 10px;
  border-color: transparent transparent transparent #cfbede;
  opacity: 0;
  transform-style: preserve-3d;
  transform: translate3d(-15px, -50%, 0px);
  transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.archive .insights .post-insight .read-more:hover:after,
.archive .insights .post-eating-ethnic .read-more:hover:after,
.archive .eating-ethnic .post-insight .read-more:hover:after,
.archive .eating-ethnic .post-eating-ethnic .read-more:hover:after {
  transform: translate3d(0px, -50%, 0px);
  opacity: 1;
}

