.post-single figure {
  display: inline-block;
  max-width: 100%;
  margin: 0;
}

.post-single .alignleft {
  float: left;
  margin-right: 10px;
}

.post-single .alignright {
  float: right;
  margin-left: 10px;
}

.post-single .post-meta-single-category {
  font-family: "Pacifico", cursive;
  text-transform: capitalize;
}

.post-single .post-meta-single-category a {
  font-size: 24px;
}

.post-single .post-title h2 {
  text-transform: uppercase;
}

.home .slider {
  position: relative;
  width: 100%;
  max-width: 62.5rem;
  height: 350px;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 640px) {
  .home .slider {
    height: 500px;
  }
}

.home .slider [class*='slide-'] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  margin-left: .9375rem;
  margin-right: .9375rem;
  background: #fff no-repeat center center;
  background-size: cover;
  transition: opacity .4s linear;
  transition: z-index 0 linear 0.4s;
  opacity: 0;
}

.home .slider [class*='slide-'].active {
  opacity: 1;
  z-index: 1;
}

.home .slider [class*='slide-'] .mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}

.home .slider [class*='slide-'] .content-of-slide {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.home .slider [class*='slide-'] .content-of-slide h2 {
  margin: 0;
  font-size: 2em;
  text-transform: uppercase;
  color: #ffffff;
}

.home .slider [class*='slide-'] .content-of-slide p {
  margin: 5px 0;
  font-size: 1.2em;
  font-weight: 300;
  color: #ffffff;
}

.home .slider [class*='slide-'] .content-of-slide .button {
  display: inline-block;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ffffff;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}

.home .slider [class*='slide-'] .content-of-slide .button:hover {
  background: #ffffff;
  color: #000;
}

.home .slider .progress-of-slides {
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 1;
  width: 100%;
  margin-left: .9375rem;
  margin-right: .9375rem;
}

.home .slider .progress-of-slides .progress-btn {
  font-size: 13px;
  text-transform: uppercase;
  width: 10px;
  height: 10px;
  background: #ffffff;
  color: #ffffff;
  float: left;
  border-radius: 5px;
  margin: 0 5px;
  overflow: hidden;
  cursor: pointer;
}

.home .slider .progress-of-slides .progress-btn:first-child {
  margin-left: 0;
}

.home .slider .progress-of-slides .progress-btn.active {
  background: #ffffff;
  color: #ffffff;
  border-color: #ffffff;
}

@media (min-width: 640px) {
  .home .slider .progress-of-slides .progress-btn {
    width: 25%;
    padding: 10px 0;
    border-top: 1px solid #ffffff;
    transition: border-color .4s linear, color .4s linear;
    height: auto;
    background: transparent;
    border-radius: 0;
    margin: 0;
  }
  .home .slider .progress-of-slides .progress-btn.active {
    background: transparent;
  }
}

.home .block-1 {
  padding-top: 60px;
  padding-bottom: 60px;
}

.home .block-1 .item a {
  position: relative;
  display: block;
  overflow: hidden;
  background-color: #212121;
  text-align: center;
  outline: none;
  text-decoration: none;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

.home .block-1 .item a:before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.home .block-1 .item a:active, .home .block-1 .item a:focus, .home .block-1 .item a:hover {
  color: #cfbede;
}

.home .block-1 .item a:active img, .home .block-1 .item a:focus img, .home .block-1 .item a:hover img {
  opacity: 0.25;
  transform: translate3d(0, 0, 0);
}

.home .block-1 .item a:active span, .home .block-1 .item a:focus span, .home .block-1 .item a:hover span {
  color: #cfbede;
